/*
# リストスタイル .ulBasic01

ここにコメントとか説明入れたりすると便利！

標準の箇条書きリストスタイル`.ulBasic01`

```
<ul class="ulBasic01">
	<li>箇条書きリスト</li>
	<li>箇条書きリスト</li>
	<li>箇条書きリスト</li>
</ul>
```
*/
.ulBasic01 li {
  padding-left: 15px;
  position: relative;
}
.ulBasic01 li:before {
  content: "";
  display: block;
  width: 4px;
  height: 4px;
  position: absolute;
  top: 7px;
  left: 2px;
  border-radius: 100%;
}

