/*////////////////////////////////////////////////////

	状態の変化があるcssを書く

	is-○○　系統

////////////////////////////////////////////////////*/
.is-view {
  overflow-y: hidden;
}
.is-view .l-gnav {
  visibility: visible;
  opacity: 1;
  z-index: 9997;
  height: 100vh;
  transition: all 0.8s 0s ease-in-out;
}
.is-view .gnav-line {
  transition: all 0.2s 0s ease-in-out;
}
.is-view .gnav-line:nth-child(2) {
  display: none;
}
.is-view .gnav-line:nth-child(1) {
  -webkit-transform-origin: center center;
  -ms-transform-origin: center center;
  transform-origin: center center;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
  top: 1px;
}
.is-view .gnav-line:nth-child(3) {
  -webkit-transform-origin: center center;
  -ms-transform-origin: center center;
  transform-origin: center center;
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg);
  top: 0px;
}
.is-view .gnav-menu {
  opacity: 1;
}

.is-load .spin {
  display: block !important;
}
.is-load .num {
  display: none;
}

.lazyload {
  opacity: 0;
}

.is-lazyload {
  opacity: 1;
  transition: all 2.6s cubic-bezier(0, 0.48, 0.12, 0.99);
}

.non-active {
  display: none !important;
}

