	language
------------------------------------------------ */
.other {
  opacity: 0;
}

.is-l-box {
  opacity: 1;
  transition: all 0.5s 0s ease;
}

#typewriter::after {
  content: "|";
  animation-name: blink;
  animation-duration: 1s;
  animation-iteration-count: infinite;
}

@keyframes blink {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}